.MonsterFilter {
  height: 30px;
  width: calc(100% - 4rem);
  margin-left: 1rem;
  background: none;
  border: none;
  border-radius: 0; /* ios is adding a border radius... */
  border-bottom: 2px solid #eee;
  color: #fff;
  font-size: 1rem;
}

.MonsterFilter:focus {
  outline: none;
}

.MonsterFilter::-webkit-input-placeholder {
  color: #eee;
}

.MonsterFilter:-ms-input-placeholder {
  color: #eee;
}

.MonsterFilter::placeholder {
  color: #eee;
}


